import React, { useState } from "react";
import axios from "axios";
import { useSnackbar } from "notistack";
import {
  Box,
  Button,
  Card,
  CardContent,
  CardHeader,
  Divider,
  Grid,
  TextField,
  Typography,
  LinearProgress,
  colors,
  Select,
  MenuItem,
} from "@material-ui/core";

export default function BusinessDetails() {
  const { enqueueSnackbar } = useSnackbar();
  const [loading, setLoading] = useState<boolean>(false);
  const [values, setValues] = useState(defaults);

  const handleChange = (e: any) => {
    setValues({
      ...values,
      [e.target.name]: e.target.value,
    });
  };

  const onSubmitHandler = () => {
    setLoading(true);
    axios
      .put("/api/reviewer/register-client", values)
      .then(async (response) => {
        setLoading(false);
        // console.log(response);
        if (response.status == 200 || response.status == 201) {
          console.log(response.data);
          enqueueSnackbar("Client registered successfully.", {
            variant: "success",
          });
        } else
          enqueueSnackbar(
            "Client registration failed. Contact the developer.",
            { variant: "error" }
          );
      })
      .catch((e) => {
        console.error(e.response.data.message);
        setLoading(false);
        enqueueSnackbar(
          "Client registration failed.\n" + e.response.data.message,
          { variant: "error" }
        );
      });
  };

  return (
    <form autoComplete="off" noValidate>
      <Card>
        {loading && <LinearProgress />}
        <CardHeader
          title="Create Business User"
          subheader="The information can be edited"
        />
        <Divider />
        <CardContent
          style={{ maxHeight: "calc(100vh - 259px)", overflowY: "auto" }}
        >
          <Grid container spacing={3}>
            <Grid item xs={12}>
              <Typography variant="h5">Business Details</Typography>
            </Grid>

            <Grid item md={6} xs={12}>
              <TextField
                fullWidth
                helperText="Please specify the business name"
                label="Business name"
                name="business_name"
                onChange={handleChange}
                required
                value={values.business_name}
                variant="outlined"
              />
            </Grid>
            <Grid item md={6} xs={12}>
              <TextField
                fullWidth
                helperText="Please specify the business's main website"
                label="Website link"
                name="website"
                onChange={handleChange}
                required
                value={values.website}
                variant="outlined"
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                fullWidth
                label="Business description"
                name="description"
                onChange={handleChange}
                value={values.description}
                variant="outlined"
                multiline
                rows={4}
              />
            </Grid>

            <Grid item xs={12}>
              <TextField
                fullWidth
                helperText="Please specify the address"
                label="Street Address"
                name="address"
                onChange={handleChange}
                required
                value={values.address}
                variant="outlined"
              />
            </Grid>
            <Grid item md={6} xs={12}>
              <TextField
                fullWidth
                label="Country"
                name="country"
                onChange={handleChange}
                required
                value={values.country}
                variant="outlined"
              />
            </Grid>
            <Grid item md={6} xs={12}>
              <TextField
                fullWidth
                label="City"
                name="city"
                onChange={handleChange}
                required
                value={values.city}
                variant="outlined"
              />
            </Grid>
            <Grid item md={6} xs={12}>
              <TextField
                fullWidth
                label="State"
                name="state"
                onChange={handleChange}
                required
                value={values.state}
                variant="outlined"
              />
            </Grid>
            <Grid item md={6} xs={12}>
              <TextField
                fullWidth
                label="ZIP"
                name="zip"
                onChange={handleChange}
                required
                value={values.zip}
                variant="outlined"
              />
            </Grid>

            <Grid item xs={12}>
              <Typography variant="h5">Personal Details</Typography>
            </Grid>
            <Grid item md={6} xs={12}>
              <TextField
                fullWidth
                helperText="Please specify the first name"
                label="First name"
                name="first_name"
                onChange={handleChange}
                required
                value={values.first_name}
                variant="outlined"
              />
            </Grid>
            <Grid item md={6} xs={12}>
              <TextField
                fullWidth
                helperText="Please specify the last name"
                label="Last name"
                name="last_name"
                onChange={handleChange}
                required
                value={values.last_name}
                variant="outlined"
              />
            </Grid>
            <Grid item md={6} xs={12}>
              <TextField
                fullWidth
                label="Email Address"
                name="email"
                onChange={handleChange}
                required
                value={values.email}
                variant="outlined"
              />
            </Grid>
            <Grid item md={6} xs={12}>
              <TextField
                fullWidth
                label="Phone Number"
                name="phone_number"
                onChange={handleChange}
                // type="number"
                value={values.phone_number}
                variant="outlined"
              />
            </Grid>
            <Grid item md={6} xs={12}>
              <TextField
                fullWidth
                label="Password"
                name="password"
                onChange={handleChange}
                required
                value={values.password}
                variant="outlined"
                type="password"
              />
            </Grid>
            <Grid item md={6} xs={12}>
              <TextField
                fullWidth
                label="Confirm Password"
                name="confirm_password"
                onChange={handleChange}
                required
                value={values.confirm_password}
                variant="outlined"
                type="password"
              />
            </Grid>
          </Grid>

          <Divider style={{ marginTop: 30, marginBottom: 20 }} />
          <Typography variant="h3">
            <a aria-hidden="true" href="#hed"></a>Halal Enforcement Director
          </Typography>
          <p style={{ color: colors.grey[700], marginTop: 10 }}>
            The halal enforcement director is appointed as a responsible
            individual from the submittingorganization. Their{" "}
            <strong>full name</strong>, <strong>phone number</strong>, and{" "}
            <strong>email address</strong> must be provided. If the HED is a
            team of individuals, provide the team name, contact number, and
            email address. This individual, or team, will be responsible for
            ensuring that the halal system is functional and monitored.
          </p>
          <Divider style={{ marginTop: 20, marginBottom: 20 }} />
          <Grid container spacing={3}>
            <Grid item xs={12}>
              <Select
                name="hed_type"
                // value={values.hed_type}
                onChange={handleChange}
                displayEmpty
                fullWidth
                variant="outlined"
              >
                <MenuItem value="INDIVIDUAL">INDIVIDUAL</MenuItem>
                <MenuItem value="TEAM">TEAM</MenuItem>
              </Select>
            </Grid>
            <Grid item xs={12}>
              <TextField
                fullWidth
                label="Halal Enforcement Director Name"
                name="hed_name"
                onChange={handleChange}
                // required
                // value={values.hed_name}
                variant="outlined"
              />
            </Grid>
            <Grid item md={6} xs={12}>
              <TextField
                fullWidth
                label="Contact Number"
                name="hed_phone_number"
                onChange={handleChange}
                type="number"
                // value={values.hed_phone_number}
                variant="outlined"
              />
            </Grid>
            <Grid item md={6} xs={12}>
              <TextField
                fullWidth
                label="Email"
                name="hed_email"
                onChange={handleChange}
                type="email"
                // value={values.hed_email}
                variant="outlined"
              />
            </Grid>
          </Grid>
        </CardContent>
        <Divider />
        <Box display="flex" justifyContent="flex-end" p={2}>
          <Button
            color="secondary"
            variant="contained"
            disabled={loading}
            onClick={onSubmitHandler}
          >
            Create
          </Button>
        </Box>
      </Card>
    </form>
  );
}

export const defaults = {
  id: "",
  business_name: "",
  website: "",
  description: "",
  address: "",
  country: "",
  city: "",
  state: "",
  zip: "",
  first_name: "",
  last_name: "",
  email: "",
  phone_number: "",
  cell_number: "",
  password: "",
  confirm_password: "",
};
