import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import clsx from "clsx";
import moment from "moment";
import PropTypes from "prop-types";
import {
  Box,
  Button,
  Card,
  CardHeader,
  Chip,
  Divider,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  TableSortLabel,
  Tooltip,
  makeStyles,
  LinearProgress,
} from "@material-ui/core";
import ArrowRightIcon from "@material-ui/icons/ArrowRight";
import axios from "axios";

import { Client } from "../common/types";
// import { getColorByClientStatus } from "./utils";
import { getRandomInt } from "../common/utils";
import ClientMenu from "./ClientMenu";

const useStyles = makeStyles(() => ({
  root: {},
  actions: {
    justifyContent: "flex-end",
  },
}));

interface PropTypes {
  className: string;
  data: Client[];
  rowClick: (client: Client) => void;
  rest: any;
}

export default function Results({
  className,
  data,
  rowClick,
  ...rest
}: PropTypes) {
  const navigate = useNavigate();
  const [loading, setLoading] = useState<boolean>(true);
  const classes = useStyles();
  const [clients, setClients] = useState<Client[]>([]);

  useEffect(() => {
    axios
      .post("/api/clients")
      .then(async (response) => {
        setLoading(false);
        // console.log(response.data);
        setClients(response.data);
      })
      .catch((e) => {
        // @TODO handle
        console.error(e);
        setLoading(false);
      });
  }, []);

  const handleRowClick = (id: number) => navigate(`/reviewer/client/${id}`);

  return (
    <Card className={clsx(classes.root, className)} {...rest}>
      {loading && <LinearProgress />}
      <CardHeader title={<strong children="Clients" />} />
      <Divider />
      <Box
        minWidth={800}
        style={{ height: "calc(100vh - 228px)", overflowY: "auto" }}
      >
        <Table>
          <TableHead>
            <TableRow>
              {/* <TableCell>
                  <strong>ID</strong>
                </TableCell> */}
              <TableCell>
                <strong>Business Name</strong>
              </TableCell>
              <TableCell>
                <strong>Owner Name</strong>
              </TableCell>
              <TableCell>
                <strong>Reviewer Name</strong>
              </TableCell>
              {/* <TableCell>
                <strong>Status</strong>
              </TableCell> */}
              <TableCell align="center">
                <strong>Facilities</strong>
              </TableCell>
              <TableCell align="center">
                <strong>Products</strong>
              </TableCell>
              <TableCell sortDirection="desc">
                <Tooltip enterDelay={300} title="Sort">
                  <TableSortLabel active direction="desc">
                    <strong>Date</strong>
                  </TableSortLabel>
                </Tooltip>
              </TableCell>
              <TableCell></TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {clients.map((client) => (
              <TableRow
                hover
                // @ts-ignore
                key={client.id}
                id={`client-${client.id as number}`}
                // @ts-ignore
                style={{ cursor: "pointer" }}
              >
                {/* <TableCell>{client.id}</TableCell> */}
                <TableCell onClick={(e) => handleRowClick(client.id as number)}>
                  {client.business_name}
                </TableCell>
                <TableCell onClick={(e) => handleRowClick(client.id as number)}>
                  {client.user.profile.first_name}{" "}
                  {client.user.profile.last_name}
                </TableCell>
                <TableCell
                  className="client-reviewer"
                  onClick={(e) => handleRowClick(client.id as number)}
                >
                  {client.reviewer.profile.first_name}{" "}
                  {client.reviewer.profile.last_name}
                </TableCell>
                {/* <TableCell onClick={(e) => handleRowClick(client.id as number)}>
                  <strong>
                    <Chip
                      // color={getColorByClientStatus(client.status)}
                      label="Pending"
                      size="small"
                    />
                  </strong>
                </TableCell> */}
                <TableCell
                  align="center"
                  onClick={(e) => handleRowClick(client.id as number)}
                >
                  <strong>{client.facilities_count}</strong>
                </TableCell>
                <TableCell
                  align="center"
                  onClick={(e) => handleRowClick(client.id as number)}
                >
                  <strong>_</strong>
                </TableCell>
                <TableCell onClick={(e) => handleRowClick(client.id as number)}>
                  {
                    // @ts-ignore
                    moment(client.date_updated).format("DD/MM/YY")
                  }
                </TableCell>
                <TableCell>
                  <ClientMenu client={client} />
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </Box>
      <Box display="flex" justifyContent="flex-end" p={2}>
        <Button
          color="primary"
          endIcon={<ArrowRightIcon />}
          size="small"
          variant="text"
        >
          View More
        </Button>
      </Box>
    </Card>
  );
}
