import React, { useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import {
  Box,
  Button,
  Divider,
  Grid,
  TextField,
  LinearProgress,
  Typography,
  Select,
  MenuItem,
} from "@material-ui/core";
import axios from "axios";
import { useSnackbar } from "notistack";

import { ClientProfile } from "../../../../models/Profile";

// interface ProfileDetailsProps {
//   setProfile: React.Dispatch<React.SetStateAction<Profile>>;
// }

const ProfileDetails = () => {
  const { enqueueSnackbar } = useSnackbar();
  const [loading, setLoading] = useState<boolean>(true);
  // const [profile, setProfile] = useState<Profile>(defaults);
  const [values, setValues] = useState<ClientProfile>(defaults);
  const { id } = useParams();

  useEffect(() => {
    axios
      .post(`/api/client/${id}/profile`)
      .then(async (response) => {
        console.log(response.data);
        setLoading(false);
        setValues(response.data);
        // setProfile(response.data);
      })
      .catch((e) => {
        // @TODO handle
        console.error(e);
        setLoading(false);
      });
  }, []);

  const handleChange = (event: any) => {
    setValues({
      ...values,
      [event.target.name]: event.target.value,
    });
  };

  const onSubmitHandler = () => {
    setLoading(true);
    // setProfile(values);
    axios
      .put("/api/profile", values)
      .then(async (response) => {
        setLoading(false);
        if (response.status == 200) {
          const profileNameTag = document.getElementById("profile-nametag");
          if (profileNameTag)
            profileNameTag.textContent = `${values.first_name} ${values.last_name}`;
          enqueueSnackbar("Profile updated successfully.", {
            variant: "success",
          });
        } else enqueueSnackbar("Profile update failed.", { variant: "error" });
      })
      .catch((e) => {
        // @TODO handle
        console.error(e.response);
        setLoading(false);
        enqueueSnackbar("Profile update failed.", { variant: "error" });
      });
  };

  return (
    <>
      <Box
        p={3}
        style={{
          height: "calc(100vh - 283px)",
          display: "flex",
          justifyContent: "center",
          overflow: "auto",
        }}
      >
        {loading && <LinearProgress />}
        <form autoComplete="off" noValidate>
          <Grid container spacing={3}>
            <Grid item xs={12}>
              <Typography variant="h3">Profile Details</Typography>
            </Grid>
            <Grid item md={6} xs={12}>
              <TextField
                className="profile-view-field"
                fullWidth
                label="First name"
                name="first_name"
                onChange={handleChange}
                required
                value={values.first_name}
                disabled
              />
            </Grid>
            <Grid item md={6} xs={12}>
              <TextField
                className="profile-view-field"
                fullWidth
                label="Last name"
                name="last_name"
                onChange={handleChange}
                required
                value={values.last_name}
                disabled
              />
            </Grid>
            {/* <Grid item md={6} xs={12}>
              <TextField
                className="profile-view-field"
                fullWidth
                label="Email Address"
                name="email"
                onChange={handleChange}
                required
                value={values.email}
              />
            </Grid> */}
            <Grid item md={6} xs={12}>
              <TextField
                className="profile-view-field"
                fullWidth
                label="Phone Number"
                name="phone_number"
                onChange={handleChange}
                // type="number"
                value={values.phone_number}
                disabled
              />
            </Grid>
            <Grid item md={6} xs={12}>
              <TextField
                className="profile-view-field"
                fullWidth
                label="Cell Number"
                name="cell_number"
                onChange={handleChange}
                type="number"
                value={values.cell_number}
                disabled
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                className="profile-view-field"
                fullWidth
                label="Street Address"
                name="address"
                onChange={handleChange}
                required
                value={values.address}
                disabled
              />
            </Grid>
            <Grid item md={6} xs={12}>
              <TextField
                className="profile-view-field"
                fullWidth
                label="Country"
                name="country"
                onChange={handleChange}
                required
                value={values.country}
                disabled
              />
            </Grid>
            <Grid item md={6} xs={12}>
              <TextField
                className="profile-view-field"
                fullWidth
                label="City"
                name="city"
                onChange={handleChange}
                required
                value={values.city}
                disabled
              />
            </Grid>
            <Grid item md={6} xs={12}>
              <TextField
                className="profile-view-field"
                fullWidth
                label="State"
                name="state"
                onChange={handleChange}
                required
                value={values.state}
                disabled
              />
            </Grid>
            <Grid item md={6} xs={12}>
              <TextField
                className="profile-view-field"
                fullWidth
                label="ZIP"
                name="zip"
                onChange={handleChange}
                required
                value={values.zip}
                disabled
              />
            </Grid>
            {/* <Grid item md={6} xs={12}>
              <TextField
                className="profile-view-field"
                fullWidth
                label="Password"
                name="password"
                onChange={handleChange}
                required
                value={values.password}
                type="password"
              />
            </Grid>
            <Grid item md={6} xs={12}>
              <TextField
                className="profile-view-field"
                fullWidth
                label="Confirm Password"
                name="confirmPassword"
                onChange={handleChange}
                required
                value={values.confirmPassword}
                type="password"
              />
            </Grid> */}
            <Grid item xs={12}>
              <Typography variant="h3" style={{ margin: "20px 0" }}>
                <a aria-hidden="true" href="#hed"></a>Halal Enforcement Director
              </Typography>
            </Grid>
            <Grid item xs={12}>
              <Select
                className="profile-view-field"
                name="hed_type"
                value={values.hed_type}
                onChange={handleChange}
                displayEmpty
                fullWidth
                disabled
              >
                <MenuItem value="INDIVIDUAL">INDIVIDUAL</MenuItem>
                <MenuItem value="TEAM">TEAM</MenuItem>
              </Select>
            </Grid>
            <Grid item xs={12}>
              <TextField
                className="profile-view-field"
                fullWidth
                label="Halal Enforcement Director Name"
                name="hed_name"
                onChange={handleChange}
                // required
                value={values.hed_name}
                disabled
              />
            </Grid>
            <Grid item md={6} xs={12}>
              <TextField
                className="profile-view-field"
                fullWidth
                label="Contact Number"
                name="hed_phone_number"
                onChange={handleChange}
                type="number"
                value={values.hed_phone_number}
                disabled
              />
            </Grid>
            <Grid item md={6} xs={12}>
              <TextField
                className="profile-view-field"
                fullWidth
                label="Email"
                name="hed_email"
                onChange={handleChange}
                type="email"
                value={values.hed_email}
                disabled
              />
            </Grid>
          </Grid>
        </form>
      </Box>
      <Divider />
      <Box display="flex" justifyContent="flex-end" p={2}>
        <Button color="secondary" variant="contained" disabled>
          Update
        </Button>
      </Box>
    </>
  );
};

const defaults: ClientProfile = {
  id: 0,
  user_id: 0,
  first_name: "",
  last_name: "",
  phone_number: "",
  cell_number: "",
  address: "",
  country: "",
  city: "",
  state: "",
  zip: "",
  avatar: "",
  created_at: "",
  updated_at: "",
  hed_type: "INDIVIDUAL",
  hed_name: "",
  hed_phone_number: "",
  hed_email: "",
};

export default ProfileDetails;
