// @ts-nocheck
import React, { useState, useEffect } from "react";
import axios from "axios";
import { Container, Grid, makeStyles } from "@material-ui/core";

import Page from "../../../../components/Page";
import CurrentRequestProgress from "./CurrentRequestProgress";
import FacilitiesStat from "./FacilitiesStat";
import ProductsStat from "./ProductsStat";
import Tasks from "./Tasks";
import LatestReviews from "./LatestReviews";

interface DashboardStats {
  current_request_id: number;
  current_request_status: string;
  current_request_progress: number;
  facility_count: number;
  product_count: number;
  has_hed: boolean;
  review_request_count: number;
}

export default function Dashboard() {
  const classes = useStyles();
  const [loading, setLoading] = useState<boolean>(true);
  const [dashboardStats, setDashboardStats] = useState<DashboardStats>({
    current_request_id: 0,
    current_request_status: "",
    current_request_progress: 0,
    facility_count: 0,
    product_count: 0,
    has_hed: false,
    review_request_count: 0,
  });

  useEffect(() => {
    axios
      .post("/api/client/dashboard")
      .then(async (response) => {
        setLoading(false);
        console.log(response.data);
        setDashboardStats(response.data);
      })
      .catch((e) => {
        // @TODO handle
        console.error(e);
        setLoading(false);
      });
  }, []);

  return (
    <Page className={classes.root} title="Dashboard">
      <Container maxWidth={false}>
        <Grid container spacing={3}>
          <Grid item lg={4} sm={6} xl={4} xs={12}>
            <CurrentRequestProgress
              id={dashboardStats.current_request_id}
              status={dashboardStats.current_request_status}
              progress={Math.floor(dashboardStats.current_request_progress)}
            />
          </Grid>
          <Grid item lg={4} sm={6} xl={4} xs={12}>
            <FacilitiesStat count={dashboardStats.facility_count} />
          </Grid>
          <Grid item lg={4} sm={6} xl={4} xs={12}>
            <ProductsStat count={dashboardStats.product_count} />
          </Grid>
          <Grid item md={6} xs={12}>
            <Tasks
              hasHed={dashboardStats.has_hed}
              checkStartFirstRequest={!!dashboardStats.review_request_count}
            />
          </Grid>
          <Grid item md={6} xs={12}>
            <LatestReviews />
          </Grid>
        </Grid>
      </Container>
    </Page>
  );
}

const useStyles = makeStyles((theme) => ({
  root: {
    // @ts-ignore
    backgroundColor: theme.palette.background.dark,
    minHeight: "100%",
    paddingBottom: theme.spacing(3),
    paddingTop: theme.spacing(3),
  },
}));
