import React, { useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import axios from "axios";
import {
  Container,
  Grid,
  makeStyles,
  Theme,
  useTheme,
  createStyles,
  AppBar,
  Tabs,
  Tab,
  Card,
  CardHeader,
  Divider,
  LinearProgress,
} from "@material-ui/core";

import { ReviewRequest } from "../../common/types";
import Page from "../../../../components/Page";
import FacilityView from "./FacilityView";
import ProductsView from "./ProductsView";

const useStyles1 = makeStyles((theme) => ({
  root: {
    // @ts-ignore
    backgroundColor: theme.palette.background.dark,
    minHeight: "100%",
    paddingBottom: theme.spacing(3),
    paddingTop: theme.spacing(3),
  },
}));

export default function SingleReviewRequestView() {
  const classes = useStyles1();

  return (
    // @ts-ignore
    <Page className={classes.root} title="Client">
      <Container maxWidth="lg" style={{ marginLeft: 0 }}>
        {/* <Toolbar /> */}
        <Grid container spacing={3}>
          {/* <Grid item lg={8} md={6} xs={12}> */}
          <Grid item md={12}>
            <SingleReviewRequest />
          </Grid>
        </Grid>
      </Container>
    </Page>
  );
}

function SingleReviewRequest() {
  const classes = useStyles3();
  const params = useParams();
  const requestId = Number.parseInt(params.id);
  const { id } = useParams(); // reviewRequestId
  const [reviewRequest, setReviewRequest] = useState<ReviewRequest | undefined>(
    undefined
  );
  const [loading, setLoading] = useState<boolean>(true);
  const [value, setValue] = React.useState(0);

  useEffect(() => {
    setLoading(true);
    axios
      .post(`/api/client/review-request/${id}`)
      .then(async (response) => {
        setLoading(false);
        // console.log(response.data);
        setReviewRequest(response.data);
      })
      .catch((e) => {
        // @TODO handle
        console.error(e);
        setLoading(false);
      });
  }, []);

  const handleChange = (event: React.ChangeEvent<{}>, newValue: number) => {
    setValue(newValue);
  };

  return (
    <form autoComplete="off" noValidate>
      <Card>
        {loading && <LinearProgress />}
        <CardHeader
          title={
            <strong children={`REVIEW: Document Submission ${requestId}`} />
          }
        />
        <Divider />
        <div className={classes.root}>
          <AppBar position="static" color="default">
            <Tabs
              value={value}
              onChange={handleChange}
              indicatorColor="primary"
              textColor="primary"
              variant="fullWidth"
              aria-label="full width tabs example"
              centered
            >
              <Tab label="Facility Documents" {...a11yProps(0)} />
              <Tab label="Products & Ingredients" {...a11yProps(1)} />
            </Tabs>
          </AppBar>
          <TabPanel value={value} index={0}>
            {reviewRequest && (
              <FacilityView facilityId={reviewRequest.facility_id as number} />
            )}
          </TabPanel>
          <TabPanel value={value} index={1}>
            <ProductsView />
          </TabPanel>
        </div>
      </Card>
    </form>
  );
}

interface TabPanelProps {
  children?: React.ReactNode;
  dir?: string;
  index: any;
  value: any;
}

function TabPanel(props: TabPanelProps) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`full-width-tabpanel-${index}`}
      aria-labelledby={`full-width-tab-${index}`}
      {...other}
    >
      {value === index && children}
    </div>
  );
}

function a11yProps(index: any) {
  return {
    id: `full-width-tab-${index}`,
    "aria-controls": `full-width-tabpanel-${index}`,
  };
}

const useStyles3 = makeStyles((theme: Theme) => ({
  root: {
    height: "calc(100vh - 166px)",
    backgroundColor: theme.palette.background.paper,
  },
}));
