/**
 * Common
 */
export type Document = FacilityDocument | ProductDocument | IngredientDocument;
export type Documents =
  | FacilityDocument[]
  | ProductDocument[]
  | IngredientDocument[];

export enum DocumentStatus {
  SUBMITTED = "SUBMITTED",
  APPROVED = "APPROVED",
  REJECTED = "REJECTED",
}

/**
 * Client types
 */
export interface Client {
  id: number | null;
  user_id: number | null;
  reviewer_id: number | null;
  facilities_count?: number | null;
  business_name: string;
  description: string;
  website: string;
  updated_at: string;
  created_at: string;
  status: ClientStatus;
  user?: any;
  reviewer?: any;
  facilities?: Facility[];
}

export enum ClientStatus {
  APPROVED = "APPROVED",
  PENDING = "PENDING",
  REJECTED = "REJECTED",
  NOT_APPLICABLE = "NOT APPLICABLE",
}

/**
 * Review types
 */
export interface ReviewRequest {
  id: number | null;
  client_id: number | null;
  reviewer_id: number | null;
  facility_id: number | null;
  client_email?: string;
  business_name: string;
  reviewer?: any;
  reviewer_email?: string;
  type: string;
  status: string;
  type_color: string;
  status_color: string;
  current_step_index: number;
  created_at: string;
  updated_at: string;
}

export enum ReviewRequestStatus {
  DRAFT = "DRAFT",
  SUBMITTED = "SUBMITTED",
  IN_REVIEW = "IN REVIEW",
  APPROVED = "APPROVED",
  REJECTED = "REJECTED",
}

export interface Review {
  id: number | null;
  businessName: string;
  ownerName: string;
  ownerEmail: string;
  date: number;
  reviewDate: number;
  reviewerName: string;
  status: ReviewStatus;
}

export enum ReviewStatus {
  APPROVED = "APPROVED",
  PENDING = "PENDING",
  REJECTED = "REJECTED",
  NOT_APPLICABLE = "NOT APPLICABLE",
}

/**
 * Certificate types
 */
export interface Certificate extends File {
  id: number | null;
  client_id: number | null;
  request_id: number | null;
  path: string;
  expires_at: string;
  created_at: string;
  updated_at: string;
}

export type CertificateDocument = Certificate;

/**
 * Report types
 */
export interface Report extends File {
  id: number | null;
  client_id: number | null;
  request_id: number | null;
  type: string;
  status: string;
  path: string;
  created_at: string;
  updated_at: string;
}

export type ReportDocument = Report;

/**
 * Facility Category types
 */
export interface Facility {
  id: number | null;
  qualified_id?: string;
  review_request_id: number | null;
  category_id: number | null;
  name: string;
  address: string;
  country: string;
  state: string;
  city: string;
  zip: string;
  updated_at: string;
  created_at: string;
}

export interface FacilityDocument extends File {
  id: number | null;
  facility_id: number | null;
  type: string; // enum
  status: string; // enum
  note: string;
  path: string;
  expires_at: string;
  updated_at: string;
  created_at: string;
}

export enum FacilityDocumentType {
  PEST_CONTROL = "PEST_CONTROL",
  INSPECTION_SHEET = "INSPECTION_SHEET",
  HIP = "HIP",
  SSOP = "SSOP",
  WATER_REPORT = "WATER_REPORT",
  RECALL_PLAN = "RECALL_PLAN",
  // new
  LEGAL_BUSINESS_DOCUMENTS = "LEGAL_BUSINESS_DOCUMENTS",
  TRACEABILITY_PLAN = "TRACEABILITY_PLAN",
  FLOWCHART_OF_PROCESSING = "FLOWCHART_OF_PROCESSING",
}

export interface FacilityCategory {
  id: number | null;
  title: string;
  code: string;
}

/**
 * Product types
 */
export interface Product {
  id: number | null;
  qualified_id?: string;
  review_request_id: number | null;
  facility_id: number | null;
  category_id: number | null;
  name: string;
  description?: string;
  preview_image: string;
  ingredients: Ingredient[];
  date: number;
  updated_at?: string;
  created_at?: string;
}

export interface ProductDocument extends File {
  id: number | null;
  product_id: number | null;
  type: string; // enum
  status: string; // enum
  note: string;
  path: string;
  expires_at: string;
  updated_at: string;
  created_at: string;
}

export enum ProductDocumentType {
  SPEC_SHEETS = "SPEC_SHEETS",
  CERTIFICATE_OF_ANALYSIS = "CERTIFICATE_OF_ANALYSIS",
  TESTING = "TESTING",
}

export interface ProductCategory {
  id: number | null;
  title: string;
  code: string;
}

/**
 * Ingredient types
 */
export interface Ingredient {
  id: number | null;
  review_request_id: number | null;
  client_id: number | null;
  product_id: number | null;
  name: string;
  manufacturer: Manufacturer | null;
  manufacturer_name?: string;
  description: string;
  recommendation: string;
  source: string;
}

export enum IngredientRecommendation {
  HALAL_ASLAN = "Halal Aslan",
  MASHBUH = "Mashbuh",
  HARAM = "Haram",
}

export enum IngredientSource {
  ANIMAL = "Animal",
  PLANT = "Plant",
  SYNTHETIC = "Synthetic",
  MINERAL = "Mineral",
  GAS = "Gas",
}

export interface IngredientDocument extends File {
  id: number | null;
  ingredient_id: number | null;
  type: string; // enum
  status: string; // enum
  note: string;
  path: string;
  expires_at: string;
  updated_at: string;
  created_at: string;
}

export enum IngredientDocumentType {
  CERTIFICATE_OR_DISCLOSURE = "CERTIFICATE_OR_DISCLOSURE",
}

export interface IngredientDocument extends File {
  id: number | null;
  ingredient_id: number | null;
  type: string; // enum
  path: string;
  expires_at: string;
  updated_at: string;
  created_at: string;
}

/**
 * Manufacturer types
 */
export interface Manufacturer {
  id?: number;
  name: string;
  created_at?: string;
  updated_at?: string;
}

/**
 * Certificate types
 */
export enum CertificateStatus {
  ISSUED,
  PENDING,
  SUSPENDED,
}
