import React, { useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import {
  Box,
  Button,
  Divider,
  Grid,
  TextField,
  Typography,
  colors,
  Select,
  MenuItem,
  CircularProgress,
} from "@material-ui/core";
import axios from "axios";
import { useSnackbar } from "notistack";

import { ClientProfile } from "../../../../models/Profile";

const ProfileDetails = () => {
  const { enqueueSnackbar } = useSnackbar();
  const { id } = useParams(); // clientId
  const [loading, setLoading] = useState<boolean>(true);
  const [values, setValues] = useState<ClientProfile>(defaults);

  useEffect(() => {
    axios
      .post(`/api/client/${id}/profile`)
      .then(async (response) => {
        console.log(response.data);
        setLoading(false);
        setValues(response.data);
        // setProfile(response.data);
      })
      .catch((e) => {
        // @TODO handle
        console.error(e);
        setLoading(false);
      });
  }, []);

  const handleChange = (event: any) => {
    setValues({
      ...values,
      [event.target.name]: event.target.value,
    });
  };

  const onSubmitHandler = () => {
    setLoading(true);
    // setProfile(values);
    axios
      .put(`/api/client/${id}/profile`, values)
      .then(async (response) => {
        setLoading(false);
        if (response.status == 200) {
          const profileNameTag = document.getElementById(
            "client-profile-nametag"
          );
          if (profileNameTag)
            profileNameTag.textContent = `${values.first_name} ${values.last_name}`;
          enqueueSnackbar("Profile updated successfully.", {
            variant: "success",
          });
        } else enqueueSnackbar("Profile update failed.", { variant: "error" });
      })
      .catch((e) => {
        // @TODO handle
        console.error(e.response);
        setLoading(false);
        enqueueSnackbar("Profile update failed.", { variant: "error" });
      });
  };

  return (
    <>
      <Box
        p={3}
        style={{
          height: "calc(100vh - 317px)",
          display: "flex",
          justifyContent: "center",
          overflowX: "auto",
        }}
      >
        {(loading && <CircularProgress />) || (
          <Box width="100%">
            <Grid container spacing={3}>
              <Grid item md={6} xs={12}>
                <TextField
                  fullWidth
                  helperText="Please specify the first name"
                  label="First name"
                  name="first_name"
                  onChange={handleChange}
                  required
                  value={values.first_name}
                  variant="outlined"
                />
              </Grid>
              <Grid item md={6} xs={12}>
                <TextField
                  fullWidth
                  label="Last name"
                  name="last_name"
                  onChange={handleChange}
                  required
                  value={values.last_name}
                  variant="outlined"
                />
              </Grid>
              {/* <Grid item md={6} xs={12}>
              <TextField
                fullWidth
                label="Email Address"
                name="email"
                onChange={handleChange}
                required
                value={values.email}
                variant="outlined"
              />
            </Grid> */}
              <Grid item md={6} xs={12}>
                <TextField
                  fullWidth
                  label="Phone Number"
                  name="phone_number"
                  onChange={handleChange}
                  // type="number"
                  value={values.phone_number}
                  variant="outlined"
                />
              </Grid>
              <Grid item md={6} xs={12}>
                <TextField
                  fullWidth
                  label="Cell Number"
                  name="cell_number"
                  onChange={handleChange}
                  // type="number"
                  value={values.cell_number}
                  variant="outlined"
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  fullWidth
                  helperText="Please specify the address"
                  label="Street Address"
                  name="address"
                  onChange={handleChange}
                  required
                  value={values.address}
                  variant="outlined"
                />
              </Grid>
              <Grid item md={6} xs={12}>
                <TextField
                  fullWidth
                  label="Country"
                  name="country"
                  onChange={handleChange}
                  required
                  value={values.country}
                  variant="outlined"
                />
              </Grid>
              <Grid item md={6} xs={12}>
                <TextField
                  fullWidth
                  label="City"
                  name="city"
                  onChange={handleChange}
                  required
                  value={values.city}
                  variant="outlined"
                />
              </Grid>
              <Grid item md={6} xs={12}>
                <TextField
                  fullWidth
                  label="State"
                  name="state"
                  onChange={handleChange}
                  required
                  value={values.state}
                  variant="outlined"
                />
              </Grid>
              <Grid item md={6} xs={12}>
                <TextField
                  fullWidth
                  label="ZIP"
                  name="zip"
                  onChange={handleChange}
                  required
                  value={values.zip}
                  variant="outlined"
                />
              </Grid>
              {/* <Grid item md={6} xs={12}>
              <TextField
                fullWidth
                label="Password"
                name="password"
                onChange={handleChange}
                required
                value={values.password}
                variant="outlined"
                type="password"
              />
            </Grid>
            <Grid item md={6} xs={12}>
              <TextField
                fullWidth
                label="Confirm Password"
                name="confirmPassword"
                onChange={handleChange}
                required
                value={values.confirmPassword}
                variant="outlined"
                type="password"
              />
            </Grid> */}
            </Grid>
            <Divider style={{ marginTop: 30, marginBottom: 20 }} />
            <Typography variant="h3">
              <a aria-hidden="true" href="#hed"></a>Halal Enforcement Director
            </Typography>
            <p style={{ color: colors.grey[700], marginTop: 10 }}>
              The halal enforcement director is appointed as a responsible
              individual from the submitting organization. Their{" "}
              <strong>full name</strong>, <strong>phone number</strong>, and{" "}
              <strong>email address</strong> must be provided. If the HED is a
              team of individuals, provide the team name, contact number, and
              email address. This individual, or team, will be responsible for
              ensuring that the halal system is functional and monitored.
            </p>
            <Divider style={{ marginTop: 20, marginBottom: 20 }} />
            <Grid container spacing={3}>
              <Grid item xs={12}>
                <Select
                  name="hed_type"
                  value={values.hed_type}
                  onChange={handleChange}
                  displayEmpty
                  fullWidth
                  variant="outlined"
                >
                  <MenuItem value="INDIVIDUAL">INDIVIDUAL</MenuItem>
                  <MenuItem value="TEAM">TEAM</MenuItem>
                </Select>
              </Grid>
              <Grid item xs={12}>
                <TextField
                  fullWidth
                  label="Halal Enforcement Director Name"
                  name="hed_name"
                  onChange={handleChange}
                  // required
                  value={values.hed_name}
                  variant="outlined"
                />
              </Grid>
              <Grid item md={6} xs={12}>
                <TextField
                  fullWidth
                  label="Contact Number"
                  name="hed_phone_number"
                  onChange={handleChange}
                  type="number"
                  value={values.hed_phone_number}
                  variant="outlined"
                />
              </Grid>
              <Grid item md={6} xs={12}>
                <TextField
                  fullWidth
                  label="Email"
                  name="hed_email"
                  onChange={handleChange}
                  type="email"
                  value={values.hed_email}
                  variant="outlined"
                />
              </Grid>
            </Grid>
          </Box>
        )}
      </Box>
      <Divider />
      <Box display="flex" justifyContent="flex-end" p={2}>
        <Button
          color="secondary"
          variant="contained"
          disabled={loading}
          onClick={onSubmitHandler}
        >
          Update
        </Button>
      </Box>
    </>
  );
};

const defaults: ClientProfile = {
  id: 0,
  user_id: 0,
  first_name: "",
  last_name: "",
  phone_number: "",
  cell_number: "",
  address: "",
  country: "",
  city: "",
  state: "",
  zip: "",
  avatar: "",
  created_at: "",
  updated_at: "",
  hed_type: "INDIVIDUAL",
  hed_name: "",
  hed_phone_number: "",
  hed_email: "",
};

export default ProfileDetails;
