import React from "react";
import { Link as RouterLink } from "react-router-dom";
import {
  Avatar,
  Card,
  CardHeader,
  Divider,
  List,
  ListItem,
  ListItemAvatar,
  ListItemText,
  ListItemSecondaryAction,
  useTheme,
} from "@material-ui/core";
import KeyboardArrowRightIcon from "@material-ui/icons/KeyboardArrowRight";
import CheckBoxOutlineBlankIcon from "@material-ui/icons/CheckBoxOutlineBlank";
import CheckBoxIcon from "@material-ui/icons/CheckBox";

interface TasksProps {
  hasHed: boolean;
  checkStartFirstRequest: boolean;
}

export default function Tasks({
  hasHed = false,
  checkStartFirstRequest = false,
}: TasksProps) {
  const theme = useTheme();

  console.log(`hasHed: ${hasHed}`);

  return (
    <Card style={{ height: "calc(100vh - 251px)", overflowY: "auto" }}>
      <CardHeader
        title="Getting Started"
        subheader="Complete the following tasks"
      />
      <Divider />
      <List>
        <ListItemLink href="/client/profile/edit">
          <ListItemAvatar>
            <Avatar
              style={{
                backgroundColor:
                  (hasHed && theme.palette.primary.main) ||
                  theme.palette.grey[400],
              }}
            >
              {(hasHed && <CheckBoxIcon />) || <CheckBoxOutlineBlankIcon />}
            </Avatar>
          </ListItemAvatar>
          <ListItemText
            primary="Elect Your Halal Enforcement Director" /*secondary="Secondary text"*/
          />
          <ListItemSecondaryAction>
            <KeyboardArrowRightIcon />
          </ListItemSecondaryAction>
        </ListItemLink>
        <ListItemLink href="/client/new-request">
          <ListItemAvatar>
            <Avatar
              style={{
                backgroundColor:
                  (checkStartFirstRequest && theme.palette.primary.main) ||
                  theme.palette.grey[400],
              }}
            >
              {(checkStartFirstRequest && <CheckBoxIcon />) || (
                <CheckBoxOutlineBlankIcon />
              )}
            </Avatar>
          </ListItemAvatar>
          <ListItemText
            primary="Start Your First Doc Submission Request" /*secondary="Secondary text"*/
          />
          <ListItemSecondaryAction>
            <KeyboardArrowRightIcon />
          </ListItemSecondaryAction>
        </ListItemLink>
        <ListItemLink href="/client/certificates">
          <ListItemAvatar>
            <Avatar>
              <CheckBoxOutlineBlankIcon />
            </Avatar>
          </ListItemAvatar>
          <ListItemText
            primary="View New Certificate" /*secondary="Secondary text"*/
          />
          <ListItemSecondaryAction>
            <KeyboardArrowRightIcon />
          </ListItemSecondaryAction>
        </ListItemLink>
      </List>
    </Card>
  );
}

function ListItemLink({ href, ...rest }: any) {
  return (
    <RouterLink to={href}>
      <ListItem button {...rest} />
    </RouterLink>
  );
}
